/* eslint-disable */

import React, { useEffect, useState } from 'react';
import Web3 from 'web3';
import Header from '../../components/header';
// import Footer from '../../components/footer';
import red from '../../assets/images/red-el.svg';
import green from '../../assets/images/green-el.svg';
import blue_crystall from '../../assets/images/blue-crystall-el.svg';
import OfficialPartners from '../../components/official-partners';
import mint_monkey from '../../assets/images/aos-3d-art-whitelist.png'; 
import mint_monkey_2x from '../../assets/images/aos-3d-art-whitelist@2x.png';
import { mintApes, getMinted, getAccounts, connectWallet } from '../../utils/web3';
// import { mainnetContract, aosContract, aosAbi, abi } from '../../constants/contract';

function Mint() {
	const [availableApes, setAvailableApes] = useState(20);
	const [mintedApes, setMintedApes] = useState(0);
	const [countApes, setCountApes] = useState('1');

	useEffect(() => {
		(async () => {
			await setSupply();
		})()
	})

	const setSupply = async () => {
		const supply = await getMinted();
		setMintedApes(supply);
	}

	// Event handlers
	const handleMinusClick = () => {
		const countApesNumber = Number(countApes);

		if (countApesNumber <= 1) {
			setCountApes('1');
			return;
		}

		setCountApes(prev => {
			const prevNumber = Number(prev);

			return String(prevNumber - 1);
		});
	};

	const handlePlusClick = () => {
		const countApesNumber = Number(countApes);
		const availableApesNumber = Number(availableApes);

		if (countApesNumber >= availableApesNumber) {
			setCountApes(availableApes);
			return;
		}

		setCountApes(prev => {
			const prevNumber = Number(prev);

			return String(prevNumber + 1);
		});
	};

	const handleInputChange = e => {
		const value = e.target.value;
		const valueNum = Number(value);
		const availableApesNumber = Number(availableApes);

		// Special requirements
		if (value.startsWith('0') && value.length >= 2) {
			setCountApes(value.slice(1));
			return;
		} else if (valueNum <= 0) {
			setCountApes('0');
			return;
		} else if (valueNum > availableApesNumber) {
			setCountApes(availableApes);
			return;
		}

		setCountApes(value);
	};

	const handleMintBtn = async () => {
		const apesNum = Number(countApes);

		if(apesNum === 0) {
			return;
		}

		let address 
		const addr = await getAccounts();
		if (addr[0]) {
			address = addr[0];
		} else {
			address = await connectWallet()
		}
		
		mintApes(apesNum, address);
	};

	return(
		<div className="mint">
			<Header />
			<div className="mint-content">
				<div className="mint-content__wrapper">
					<img data-img="red-1" src={red} alt="" />
					<img data-img="blue-1" src={blue_crystall} alt="" />
					<img data-img="mint-monkey" src={mint_monkey} srcSet={`${mint_monkey} 1x, ${mint_monkey_2x} 2x`} alt="" />
					<div className="mint-content__block">
						<img data-img="green-1" src={green} alt="" />
						<img data-img="blue-2" src={blue_crystall} alt="" />
						<h1 className="title title_size-l mint-content__title">Minted Apes of space VX: {mintedApes}</h1>
						<p className="title title_size-m mint-content__subtitle">Mint price: 0.1 ETH + Gas Fee</p>
						<p className="title title_size-m mint-content__sub">Max per wallet: 20 NFT</p>
						<div className="mint-content__count">
							<button type="button" className="btn btn_blue mint-content__count-btn btn-minus" onClick={handleMinusClick}>
								<span></span>
							</button>
							<input 
								type="number" 
								className="mint-input mint-content__count-input" 
								name="apes_count" 
								value={countApes}
								onChange={evt => handleInputChange(evt)}
							></input>
							<button type="button" className="btn btn_blue mint-content__count-btn btn-plus" onClick={handlePlusClick}>
								<span></span>
								<span></span>
							</button>
						</div>
						<div className="mint-content__btn-wrapper">
							<button type="button" className="btn btn_yellow mint-content__btn" onClick={handleMintBtn}>
								Mint aos vx
							</button>
						</div>
						<div className="mint-content__note">
							<p>
								The Sandbox Premium Partners. 6x6 ESTATE. Apes of Space VX is an NFT ticket that provides access for players to explore unique game experiences. 
								Earn $SAND tokens and other exclusive rewards!
							</p>
							<br />
							<p>
								IMPORTANT. Total supply: 7777 NFTs. 1777 NFTs from total Supply will be available for free claim for genesis Apes of Space and Lady Apes NFT holders for 1 month.
							</p>
						</div>
					</div>
				</div>
			</div>
			<OfficialPartners />
		</div>
	);
}

export default Mint;